<template>
  <div class="c-app flex-row align-items-center">
    <CContainer>
      <CRow class="justify-content-center">
        <CCol md="8">
          <CCardGroup>
            <CCard class="p-4">
              <CCardBody>
                <CForm  @submit.prevent="login" validate>
                  <h1>Login</h1>
                  <p class="text-muted">Sign In to your account</p>
                  <CInput v-model="email"
                    placeholder="Username"
                    autocomplete="username email"
                    required
                  >
                    <template #prepend-content><CIcon name="cil-user"/></template>
                  </CInput>
                  <CInput  v-model="password"
                    placeholder="Password"
                    type="password"
                    autocomplete="curent-password"
                    required
                  >
                    <template #prepend-content><CIcon name="cil-lock-locked"/></template>
                  </CInput>
                  <CRow>
                    <CCol col="6" class="text-left">
                      <CButton type="submit" color="primary" class="px-4">Login</CButton>
                    </CCol>
                    <CCol col="6" class="text-right">
                      <CButton color="link" class="px-0">Forgot password?</CButton>
                      <CButton color="link" class="d-lg-none">Register now!</CButton>
                    </CCol>
                  </CRow>
                </CForm>
              </CCardBody>
            </CCard>
            <CCard
              color="primary"
              text-color="white"
              class="text-center py-5 d-md-down-none"
              body-wrapper
            >
              <CCardBody>
                <h2>Sign up</h2>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                <CButton @click="$router.push('/register')"
                  color="light"
                  variant="outline"
                  size="lg"
                >
                  Register Now!
                </CButton>
              </CCardBody>
            </CCard>
          </CCardGroup>
        </CCol>
      </CRow>
    </CContainer>
  </div>
</template>

<script>
export default {
  name: 'Login',
      data() {
        return {
            email: '',
            password: '',
        }
    },
methods: {
login(){
  let loader = this.$loading.show();
   this.axios.post("session", {"email":this.email,"hashedpassword":this.password})
            .then(response => {
              loader.hide();
              let user = response.data
              this.$cookies.set('user',user);
              this.$cookies.set('token',user.token);
              this.$router.push('/dashboard');

            }).catch(error=>{
               loader.hide();
               this.$toastr.e("error MESSAGE", "error Toast Title");
            });
}
  }
}
</script>
